@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

body{
    margin: 0;
    padding: 0;
}

//css for deel
.deel1{
    display: flex;
    flex-direction: row;
    width: 100vw;
